.App {
  font-family: sans-serif;
  text-align: center;
  /* background-color: blue; */
  background-size: 100%;
}
.grid-container {
  /* background-color:cadetblue */
  display: grid;
  grid-template-columns: auto auto auto;
  padding: 10px;
}
.grid-item {
  background-color: cadetblue;
  border: 1px solid rgba(0, 0, 0, 0.8);
  padding: 20px;
  font-size: 30px;
  text-align: center;
}
.inlineDisply {
  display: table-cell;
  width: 30px;
}
.stylingproduct {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 400px;
  padding: 30px;
  border-radius: 10px;
  background-color: #f7d5f1;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.6);
}
.imagalign {
  /* display: flex-direction; */
  /* flex-direction: column-reverse; */
  image-orientation: lightseagreen;
  padding-left: 500px;
  width: 200px;
  border-radius: 10px;

  /* background-color: lightseagreen; */
  /* box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.6); */
}
